import React, { useState } from 'react';
import { Form, Formik } from 'formik';
import Input from '../../Common/Input';
import Select from '../../Common/Select';
import Checkbox from '../../Common/Checkbox';
import PizZip from 'pizzip';
import Docxtemplater from 'docxtemplater';
import Phototable_template from '../../Assets/   .docx';
import Act_template from '../../Assets/    .doc';
import Schema_template from '../../Assets/   .docx';
import Task_template from '../../Assets/   .doc';

const PatrolReportGenerator = () => {
  const [isFresh, setIsFresh] = useState(false);

  //  
  const custom_alert = (message) => {
    return window.Swal.fire({
        title: message,
        width: 400,
        height: 200,
        padding: '1em',
        color: 'green',
        background: '#fff url(/pictures/trees_phone.png)',
    });
  }

  const date_formation = (date) => {
    const month_names = [
      {'01': ''},
      {'02': ''},
      {'03': ''},
      {'04': ''},
      {'05': ''},
      {'06': ''},
      {'07': ''},
      {'08': ''},
      {'09': ''},
      {'10': ''},
      {'11': ''},
      {'12': ''},
    ];

    // 1.    
    const month_number = date.split("-")[1];
    // 2.   
    const foundMonth = month_names.find(month => month[month_number]);
    // 3.   ,   
    const monthName = foundMonth ? foundMonth[month_number] : '  ';
    // 4.  
    return `${date.split("-")[2]} ${monthName} ${date.split("-")[0]}`;
  }

  const patrol_participants = [
    {id: '1', value: '       ', name_option: ',  ', disabled: false},
    {id: '2', value: '       ', name_option: ',  ', disabled: false},
    {id: '3', value: '       ', name_option: ',  ', disabled: false},
    {id: '4', value: '       ', name_option: ',  ', disabled: false},
  ]

  return (
    <div className='uk-margin-small-bottom'>
      <h1>  </h1>

      <Formik
        initialValues={{ Patrol_task_number: undefined, Date_issue_task: '', Patrol_date: '', 
          Patrol_route_number: '', What_date_was_approved: '28.10.2024', Year_patrol: 2025,
          Person_issued_task: '  " "  ..',
          Full_name_senior_patroller: '       ', 
          Full_name_junior_patroller: '       ', 
          Time_assignment_issue: '', Additional_order: false, 
          Number_patrol_act: undefined, Object_leaflet: '', Village_leaflet: '', 
          Number_informed_people: '', Village_informed_people: '', Patrol_car: '948',
          Is_there_photo_table: false 
        }}
        validate={values => {
          const errors = {};

          if (!values.Patrol_task_number) {
            errors.Patrol_task_number = '  ';
          } else if (!/^\d{1,4}$/i.test(values.Patrol_task_number)) {
            errors.Patrol_task_number = '      4 ';
          }

          if (!values.Date_issue_task) {
            errors.Date_issue_task = '   ';
          } 

          if (!values.Patrol_date) {
            errors.Patrol_date = '  ';
          }

          if (!values.Patrol_route_number) {
            errors.Patrol_route_number = '   ';
          } 

          if (!values.What_date_was_approved) {
            errors.What_date_was_approved = '   ';
          } else if (!/^\d{2}.\d{2}.\d{4}$/i.test(values.What_date_was_approved)) {
            errors.Patrol_task_number = '    08.12.2023';
          }

          if (!values.Year_patrol) {
            errors.Year_patrol = '  ';
          } 

          if (!values.Person_issued_task) {
            errors.Person_issued_task = '  ';
          } 

          if (!values.Full_name_senior_patroller) {
            errors.Full_name_senior_patroller = '   ';
          } 

          if (!values.Full_name_junior_patroller) {
            errors.Full_name_junior_patroller = ' ';
          }

          if (!values.Time_assignment_issue) {
            errors.Time_assignment_issue = '   ';
          } 

          if (!values.Number_patrol_act) {
            errors.Number_patrol_act = '   ';
          } else if (!/^\d{1,4}$/i.test(values.Number_patrol_act)) {
            errors.Number_patrol_act = '      4 ';
          }

          if (!values.Object_leaflet) {
            errors.Object_leaflet = '  ';
          }

          if (!values.Village_leaflet) {
            errors.Village_leaflet = '  ';
          }

          if (!values.Village_leaflet) {
            errors.Village_leaflet = '  ';
          }

          if (!values.Number_informed_people) {
            errors.Number_informed_people = '  ';
          }

          if (!values.Village_informed_people) {
            errors.Village_informed_people = '  ';
          }

          if (!values.Patrol_car) {
            errors.Patrol_car = '  ';
          }

          return errors;
        }}
        onSubmit={(values, actions) => {
          setTimeout(() => {
            // alert(JSON.stringify(values, null, 2));

            // 1.     
            const Person_issued_task = values.Person_issued_task;
            const Patrol_task_number = values.Patrol_task_number;
            let Date_issue_task = values.Date_issue_task; 
            let Patrol_date = values.Patrol_date; 
            const Patrol_route_number = values.Patrol_route_number; 
            const What_date_was_approved = values.What_date_was_approved; 
            const Year_patrol = values.Year_patrol; 
            const Full_name_senior_patroller = values.Full_name_senior_patroller; 
            const Full_name_junior_patroller = values.Full_name_junior_patroller; 
            let Time_assignment_issue = values.Time_assignment_issue;
            const Additional_order = values.Additional_order === true ? " (   _____._____._____   _____)" : "";
            const Number_patrol_act = values.Number_patrol_act; 
            
            function generate_date_patrol_act() {
              //   
              const date = values.Patrol_date;
              //             
              let [day, month, year] = [date.split("-")[2], date.split("-")[1], date.split("-")[0]];
              //  ,      
              day = +day + 1;
              //     10,   ,   
              day = day < 10 ? `0${day}` : `${day}`;
              //   ,      
              return `${year}-${month}-${day}`;
            }

            let Date_patrol_act = generate_date_patrol_act();

            const Object_leaflet = values.Object_leaflet; 
            const Village_leaflet = values.Village_leaflet; 
            const Number_informed_people = values.Number_informed_people; 
            const Village_informed_people = values.Village_informed_people; 
            const Patrol_car = values.Patrol_car; 
            const Is_there_photo_table = values.Is_there_photo_table === true ? ", " : "";

            // 2.  ,  

            // 2.1.    
            Time_assignment_issue = `${Time_assignment_issue.split(":")[0]}  ${Time_assignment_issue.split(":")[1]} `;

            // 2.2.    
            Date_issue_task = date_formation(Date_issue_task);
            Patrol_date = date_formation(Patrol_date);
            Date_patrol_act = date_formation(Date_patrol_act);

            // 2.3.       
            let Senior_patroller, Declension_name_senior_patroller, Initials_senior_patroller, 
            Junior_patroller, Declension_name_junior_patroller, Initials_junior_patroller,
            Patrol_route_declaration;

            // 2.4.     
            switch (Full_name_senior_patroller) {
                case '       ':
                    Senior_patroller = '      ..';
                    Declension_name_senior_patroller = '       ';
                    Initials_senior_patroller = ' ..';
                    break;
                case '       ':
                    Senior_patroller = '      ..';
                    Declension_name_senior_patroller = '       ';
                    Initials_senior_patroller = ' ..';
                    break;
                case '       ':
                    Senior_patroller = '      ..';
                    Declension_name_senior_patroller = '       ';
                    Initials_senior_patroller = ' ..';
                    break;
                case '       ':
                    Senior_patroller = '      ..';
                    Declension_name_senior_patroller = '       ';
                    Initials_senior_patroller = ' ..';
                    break;
                case '       ':
                    Senior_patroller = '      ..';
                    Declension_name_senior_patroller = '       ';
                    Initials_senior_patroller = ' ..';
                    break;
                default:
                    break;
            }

            // 2.5.     
            switch (Full_name_junior_patroller) {
                case '       ':
                    Junior_patroller = '      ..';
                    Declension_name_junior_patroller = '       ';
                    Initials_junior_patroller = ' ..';
                    break;
                case '       ':
                    Junior_patroller = '      ..';
                    Declension_name_junior_patroller = '       ';
                    Initials_junior_patroller = ' ..';
                    break;
                case '       ':
                    Junior_patroller = '      ..';
                    Declension_name_junior_patroller = '       ';
                    Initials_junior_patroller = ' ..';
                    break;
                case '       ':
                    Junior_patroller = '      ..';
                    Declension_name_junior_patroller = '       ';
                    Initials_junior_patroller = ' ..';
                    break;
                case '       ':
                    Junior_patroller = '      ..';
                    Declension_name_junior_patroller = '       ';
                    Initials_junior_patroller = ' ..';
                    break;
                default:
                    break;
            }

            // 2.6.         
            let Patrol_time, Length_patrol;

            // 2.7.    
            switch (Patrol_route_number) {
                case '106':
                    Patrol_route_declaration = ' 128 (  14,17,26,30,36,38),  126 (  8,7,15),  228 (  13,21,25),  216 (  17,18,19,23,30),  213 (  20,24,16),  211 (  20,21,24,30,32),  199 (  21,22,31,33,35)';
                    Patrol_time = '35 .';
                    Length_patrol = '14 .';
                    break;
                case '107':
                    Patrol_route_declaration = ' 200 (  49,50,51,4,6,7),  191 (  25,26,27),  40 (  19,20),  201 (  6,8,14,15,18,24,26,28,31),  195 (  11,12),  196 (  2,10,11,12)';
                    Patrol_time = '15 .';
                    Length_patrol = '5 .';
                    break;
                case '108':
                    Patrol_route_declaration = ' 43 (  7,9),  30 (  12,15,17,19),  192 (  3,13)';
                    Patrol_time = '60 .';
                    Length_patrol = '5 .';
                    break;
                case '109':
                    Patrol_route_declaration = ' 229 (  2,4,8,9,10,12,13,6,7,11),  116 (  9,13,15,7,18,5,11,12,16),  117 (  18,15,12,16,17,18,21),  119 (  7,16,3,2,17,6,13,14,15)';
                    Patrol_time = '10 .';
                    Length_patrol = '4 .';
                    break;
                case '110':
                    Patrol_route_declaration = ' 252 (  18)';
                    Patrol_time = '10 .';
                    Length_patrol = '0,3 .';
                    break;
                case '111':
                    Patrol_route_declaration = '  255 (  2,4,6,7,9),  254 (  2,3,5,7,8,9,16,19),  248 (  26,27,28),  247 (  35,36,39,46,49,38,45,47,48,51,53,4,6,7,8,5,19,18,23),  232 (  2,4,3,5,6,10,11,21),  233 (  4,5,3,7,2),  230 (  2,3,4,8,9,10,11,12)';
                    Patrol_time = '20 .';
                    Length_patrol = '8 .';
                    break;
                case '112':
                    Patrol_route_declaration = ' 226 (  1,2,4,3,9,8,12),  111 (  4,7,8),  106 (  6,16,5,8),  208 (  6,17,20,10,9,5,7),  207 (  8,7,9,15,16,17),  197 (  9,12,8,14,11,18)';
                    Patrol_time = '1 . 20 .';
                    Length_patrol = '20 .';
                    break;
                case '113':
                    Patrol_route_declaration = ' 246 (  17,18,19,20,21,22),  155 (  14,26,28,31,13,15,23,40,30),  244 (  4,5,10,8,9,36,47,53),  151 (  9,18,17,12,13),  243 (  12)';
                    Patrol_time = '1 . 45 .';
                    Length_patrol = '12 .';
                    break;
                case '114':
                    Patrol_route_declaration = ' 225 (  39,40,47,61,21,29,32),  218 (  14,11,12,13,20,19),  86 (  19,20,34,35,33,36)';
                    Patrol_time = '40 .';
                    Length_patrol = '12 .';
                    break;
                case '115':
                    Patrol_route_declaration = ' 82 (  5,6,14,20,25),  100 (  14,19,21),  105 (  3,10,14,17,18),  110 (  9,8,17)';
                    Patrol_time = '1 .';
                    Length_patrol = '5 .';
                    break;
                case '116':
                    Patrol_route_declaration = ' 240 (  1,2,3,4,5,6,13,11,37,38,41,27,29)';
                    Patrol_time = '15 .';
                    Length_patrol = '0,5 .';
                    break;
                case '117':
                    Patrol_route_declaration = ' 250 (  2,3,5),  251 (  1,3)';
                    Patrol_time = '5 .';
                    Length_patrol = '1 .';
                    break;
                case '118':
                    Patrol_route_declaration = ' 252 (  3,5,6)';
                    Patrol_time = '40 .';
                    Length_patrol = '1 .';
                    break;
                case '119':
                    Patrol_route_declaration = ' 224 (  31,32,37,38,41),  223 (  33,36,38,45,49,54,61)';
                    Patrol_time = '10 .';
                    Length_patrol = '2 .';
                    break;
                case '120':
                    Patrol_route_declaration = ' 262 (  27)';
                    Patrol_time = '15 .';
                    Length_patrol = '0,5 .';
                    break;
                case '121':
                    Patrol_route_declaration = ' 235 (  16,20)';
                    Patrol_time = '30 .';
                    Length_patrol = '1 .';
                    break;
                case '122':
                    Patrol_route_declaration = ' 259 (  2)';
                    Patrol_time = '30 .';
                    Length_patrol = '1 .';
                    break;
                case '123':
                    Patrol_route_declaration = ' 235 (  16,20,23,24)';
                    Patrol_time = '40 .';
                    Length_patrol = '1 .';
                    break;
                case '124':
                    Patrol_route_declaration = ' 12 (  7,10),  13 (  1,23,40,35)';
                    Patrol_time = '10 .';
                    Length_patrol = '4 .';
                    break;
                default:
                    break;
            }

            // 3.  
            fetch(Task_template)
              .then(response => {
                  if (!response.ok) {
                      throw new Error('Network response was not ok ' + response.statusText);
                  }
                  return response.arrayBuffer();
              })
              .then(data => {
                  const zip = new PizZip(data);
                  const doc = new Docxtemplater(zip, {
                      paragraphLoop: true,
                      linebreaks: true,
                  });

                  //   ( {user_name}   , {user_surname}     ..)
                  doc.render({
                      Full_name_senior_patroller, Senior_patroller, Full_name_junior_patroller, 
                      Junior_patroller, Additional_order, Time_assignment_issue, 
                      Patrol_task_number, Date_issue_task, Patrol_date, Patrol_route_number, What_date_was_approved, 
                      Year_patrol, Person_issued_task
                  });

                  //     
                  const out = doc.getZip().generate({
                      type: "blob",
                      mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                  });

                  //      
                  const link = document.createElement('a');
                  link.href = URL.createObjectURL(out);
                  link.download = `+1.       -  ${Patrol_task_number}.docx`;
                  link.click();
              })
              .catch(error => {
                  console.error(':', error);
                  alert('!   console');
              });

            fetch(Act_template)
              .then(response => {
                  if (!response.ok) {
                      throw new Error('Network response was not ok ' + response.statusText);
                  }
                  return response.arrayBuffer();
              })
              .then(data => {
                  const zip = new PizZip(data);
                  const doc = new Docxtemplater(zip, {
                      paragraphLoop: true,
                      linebreaks: true,
                  });

                  //    
                  const Initial_report = `    ${Number_informed_people}   ${Village_informed_people}          .         .             ${Village_leaflet} (${Object_leaflet}).`;

                  //   ( {user_name}   , {user_surname}     ..)
                  doc.render({
                      Declension_name_senior_patroller, Initials_senior_patroller, Declension_name_junior_patroller, 
                      Initials_junior_patroller, Number_patrol_act, Date_patrol_act, Patrol_task_number, Date_issue_task, 
                      Patrol_route_declaration, Initial_report, Patrol_car, Length_patrol, Is_there_photo_table
                  });

                  //     
                  const out = doc.getZip().generate({
                      type: "blob",
                      mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                  });

                  //      
                  const link = document.createElement('a');
                  link.href = URL.createObjectURL(out);
                  link.download = `2.       -  ${Number_patrol_act}.docx`;
                  link.click();
              })
              .catch(error => {
                  console.error(':', error);
                  alert('!   console');
              });

            fetch(Schema_template)
              .then(response => {
                  if (!response.ok) {
                      throw new Error('Network response was not ok ' + response.statusText);
                  }
                  return response.arrayBuffer();
              })
              .then(data => {
                  const zip = new PizZip(data);
                  const doc = new Docxtemplater(zip, {
                      paragraphLoop: true,
                      linebreaks: true,
                  });

                  //   ( {user_name}   , {user_surname}     ..)
                  doc.render({
                      Initials_senior_patroller, Number_patrol_act, Date_patrol_act, Patrol_time, Length_patrol
                  });

                  //     
                  const out = doc.getZip().generate({
                      type: "blob",
                      mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                  });

                  //      
                  const link = document.createElement('a');
                  link.href = URL.createObjectURL(out);
                  link.download = `+3.      -.docx`;
                  link.click();
              })
              .catch(error => {
                  console.error(':', error);
                  alert('!   console');
              });

            if (values.Is_there_photo_table === true) {
              fetch(Phototable_template)
                .then(response => {
                    if (!response.ok) {
                        throw new Error('Network response was not ok ' + response.statusText);
                    }
                    return response.arrayBuffer();
                })
                .then(data => {
                    const zip = new PizZip(data);
                    const doc = new Docxtemplater(zip, {
                        paragraphLoop: true,
                        linebreaks: true,
                    });

                    //   ( {user_name}   , {user_surname}     ..)
                    doc.render({
                        Number_patrol_act, Date_patrol_act, Patrol_date, Object_leaflet, Village_leaflet,
                        Number_informed_people, Village_informed_people
                    });

                    //     
                    const out = doc.getZip().generate({
                        type: "blob",
                        mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                    });

                    //      
                    const link = document.createElement('a');
                    link.href = URL.createObjectURL(out);
                    link.download = `4.      -.docx`;
                    link.click();
                })
                .catch(error => {
                    console.error(':', error);
                    alert('!   console');
                });
            }

            actions.setSubmitting(false);
            custom_alert('    ,       ');
            setIsFresh(true);

            window.scrollTo(0, 0);
          }, 500);
        }}
      >
        {({ errors, touched, isSubmitting, handleChange, ...props }) => (
          <Form>

            {/* -------------------------------------  --------------------------------- */}

            <div className='uk-margin'>
              <h2 className='uk-heading-divider'></h2>
            </div>

            <Input 
              handleChange={handleChange} 
              isFresh={isFresh} 
              setIsFresh={setIsFresh}
              label_name=' ' 
              field_name='Patrol_task_number' 
              placeholder='185' 
              input_type='number' 
              is_primary={true} />       
            <Input 
              handleChange={handleChange} 
              isFresh={isFresh} 
              setIsFresh={setIsFresh}
              label_name='  ' 
              field_name='Date_issue_task' 
              placeholder='' 
              input_type='date' 
              is_primary={false} />   
            <Input 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name=' ' 
              field_name='Patrol_date' 
              placeholder='' 
              input_type='date' 
              is_primary={true} />      
            
            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name='  ' 
              field_name='Patrol_route_number' 
              options_list={[
                {id: '1', value: '', name_option: '', disabled: true},
                {id: '2', value: '106', name_option: '106', disabled: false},
                {id: '3', value: '107', name_option: '107', disabled: false},
                {id: '4', value: '108', name_option: '108', disabled: false},
                {id: '5', value: '109', name_option: '109', disabled: false},
                {id: '6', value: '110', name_option: '110', disabled: false},
                {id: '7', value: '111', name_option: '111', disabled: false},
                {id: '8', value: '112', name_option: '112', disabled: false},
                {id: '9', value: '113', name_option: '113', disabled: false},
                {id: '10', value: '114', name_option: '114', disabled: false},
                {id: '11', value: '115', name_option: '115', disabled: false},
                {id: '12', value: '116', name_option: '116', disabled: false},
                {id: '13', value: '117', name_option: '117', disabled: false},
                {id: '14', value: '118', name_option: '118', disabled: false},
                {id: '15', value: '119', name_option: '119', disabled: false},
                {id: '16', value: '120', name_option: '120', disabled: false},
                {id: '17', value: '121', name_option: '121', disabled: false},
                {id: '18', value: '122', name_option: '122', disabled: false},
                {id: '19', value: '123', name_option: '123', disabled: false},
                {id: '20', value: '124', name_option: '124', disabled: false},
              ]} 
              is_primary={true}           
            />

            <Input 
              handleChange={handleChange} 
              isFresh={isFresh} 
              setIsFresh={setIsFresh}
              label_name='  ' 
              field_name='What_date_was_approved' 
              placeholder='08.12.2023' 
              input_type='text' 
              is_primary={false} />   
            
            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name=' ' 
              field_name='Year_patrol' 
              options_list={[
                {id: '1', value: '2025', name_option: '2025', disabled: false},
                {id: '2', value: '2026', name_option: '2026', disabled: false},
                {id: '3', value: '2027', name_option: '2027', disabled: false},
                {id: '4', value: '2028', name_option: '2028', disabled: false},
              ]} 
              is_primary={false} 
            />

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name='  ' 
              field_name='Person_issued_task' 
              options_list={[
                {id: '1', value: '  " "  ..', name_option: '', disabled: false},
                {id: '2', value: '   " "  ..', name_option: '', disabled: false},
              ]} 
              is_primary={false} 
            />

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name='  ' 
              field_name='Full_name_senior_patroller' 
              options_list={patrol_participants}   
              is_primary={false}         
            />

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name='  ' 
              field_name='Full_name_junior_patroller' 
              options_list={patrol_participants} 
              is_primary={false}           
            />

            <Input 
              handleChange={handleChange} 
              isFresh={isFresh}
              setIsFresh={setIsFresh}
              label_name='  ' 
              field_name='Time_assignment_issue' 
              placeholder='' 
              input_type='time' 
              is_primary={true} />    

            <Checkbox name="Additional_order" label=" " />

            {/* -------------------------------------  --------------------------------- */}

            <div className='uk-margin'>
              <h2 className='uk-heading-divider'></h2>
            </div>

            <Input 
              handleChange={handleChange} 
              isFresh={isFresh}
              setIsFresh={setIsFresh}
              label_name='   ' 
              field_name='Number_patrol_act' 
              placeholder='105' 
              input_type='number' 
              is_primary={true} />   

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name='  ' 
              field_name='Object_leaflet' 
              options_list={[
                {id: '1', value: '', name_option: '', disabled: true},
                {id: '2', value: ' ', name_option: '', disabled: false},
                {id: '3', value: ' ', name_option: ' ', disabled: false},
              ]} 
              is_primary={false}           
            />

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name=',   ' 
              field_name='Village_leaflet' 
              options_list={[
                {id: '1', value: '', name_option: '', disabled: true},
                {id: '2', value: '', name_option: '', disabled: false},
                {id: '3', value: '', name_option: '', disabled: false},
                {id: '4', value: '', name_option: '', disabled: false},
                {id: '5', value: '', name_option: '', disabled: false},
                {id: '6', value: '', name_option: '', disabled: false},
                {id: '7', value: '', name_option: '', disabled: false},
              ]}  
              is_primary={false}           
            />

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name='  ' 
              field_name='Number_informed_people' 
              options_list={[
                {id: '1', value: '', name_option: '',disabled: true},
                {id: '2', value: '1', name_option: '1', disabled: false},
                {id: '3', value: '2', name_option: '2', disabled: false},
                {id: '4', value: '3', name_option: '3', disabled: false},
                {id: '5', value: '4', name_option: '4', disabled: false},
                {id: '6', value: '5', name_option: '5', disabled: false},
                {id: '7', value: '6', name_option: '6', disabled: false},
                {id: '8', value: '7', name_option: '7', disabled: false},
              ]}  
              is_primary={false}           
            />

            <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name=',    ' 
              field_name='Village_informed_people' 
              options_list={[
                {id: '1', value: '', name_option: '', disabled: true},
                {id: '2', value: '', name_option: '', disabled: false},
                {id: '3', value: '', name_option: '', disabled: false},
                {id: '4', value: '', name_option: '', disabled: false},
                {id: '5', value: '', name_option: '', disabled: false},
                {id: '6', value: '', name_option: '', disabled: false},
                {id: '7', value: ' ', name_option: ' ', disabled: false},
                {id: '8', value: ' ', name_option: ' ', disabled: false},
              ]}  
              is_primary={false}           
            />

            {/* <Select 
              handleChange={handleChange}  
              setIsFresh={setIsFresh}
              isFresh={isFresh}
              label_name=' ' 
              field_name='Patrol_car' 
              options_list={[
                {id: '1', value: '', name_option: '', disabled: true},
                {id: '2', value: '948', name_option: ' ', disabled: false},
                {id: '3', value: 'C848', name_option: ' ', disabled: false},
              ]}  
              is_primary={false}           
            /> */}

            <Checkbox name="Is_there_photo_table" label="  ?" />

            {/* --------------------------------------------------------------------------- */}

            <div className="uk-margin">
              <button type="submit" disabled={isSubmitting} className="uk-button uk-button-primary uk-button-large">
                   
              </button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  )
};

export default PatrolReportGenerator;